.checkoutProduct {
  display: flex;
  margin-top: 20px;
  margin-bottom: 20px;

}
.checkoutProduct__image {
  object-fit: contain;
  width: 180px;
  height: 180px;
}
.checkoutProduct__info {
  padding-left: 20px;
}
.checkoutProduct__title {
  font-size: 17px;
  font-weight: 800;
}
.checkoutProduct__info > button {
  background: #f0c14b;
  border: 1px solid;
  border-color: #a88734 #9c7e31 #846a29;
  color: #111111;
  margin-top: 10px;
  padding: 10px 20px;
  border-radius: 10px;
  cursor: pointer;
}
.checkoutProduct__info > button:focus {
  outline: none;
}
.checkoutProduct__rating {
  display: flex;
}
.checkoutProduct__star {
  color: yellow;
}
