.subtotal{
    width:300px;
    height: 100px;
    padding: 10px;
    background: #f3f3f3;
    border: 1px solid #dddddd;
    border-radius: 3px;
}
.subtotal__gift{
    margin-top: 5px;
    margin-bottom: 5px;
    display: flex;
    align-items: center;
}
.subtotal__gift>input{
    margin-right: 5px;
}
.subtotal>button{
    background: #f0c14b;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111111;
}